<template>
    <v-container
        id="scan"
        class="d-flex flex-column justify-center align-center"
    >
        <div class="d-flex mb-4">
            <v-btn
                v-for="(n, i) in scanComponents"
                :key="i"
                class="mx-2"
                @click="changeScanMode(n.name)"
            >
                {{ n.name }}
            </v-btn>
        </div>
        <div>
            <DecodeByUpload v-if="scanMode == 'Upload'" />
            <DecodeStream v-if="scanMode == 'Stream'" />
        </div>
    </v-container>
</template>

<script>
import DecodeByUpload from "@/components/Scans/DecodeByUpload";
import DecodeStream from "@/components/Scans/DecodeStream";
export default {
    name: "QrCodeScans",

    components: {
        DecodeByUpload,
        DecodeStream,
    },

    data() {
        return {
            scanMode: "Upload",
            scanComponents: [
                {
                    name: "Upload",
                    status: true,
                },
                {
                    name: "Stream",
                    status: false,
                },
            ],
            result: "",
        };
    },

    methods: {
        changeScanMode(n) {
            this.scanMode = n;
        },
    },
};
</script>

<style scoped>
#scan {
    min-height: 100vh;
}
</style>
