<template>
    <div>
        <p class="decode-result">
            Last result: <b>{{ result }}</b>
        </p>

        <qrcode-capture @decode="onDecode" />
        <v-dialog
            v-model="dialogSuccess"
            persistent
            max-width="400"
            transition="scroll-y-transition"
        >
            <v-card class="pb-6">
                <v-card-title class="headline d-flex justify-center">
                    <v-icon size="150" :color="dialogText == true ? '#a5dc86' : 'red' ">
                        {{ dialogText == true ? 'mdi-check-circle-outline' : 'mdi-close' }}
                    </v-icon>
                </v-card-title>
                <v-card-text class="text-center">
                    <span class="text-h4 font-weight-bold">
                        {{ dialogText == true ? 'Good Job!' : 'Not Good!' }}
                    </span>
                    <br />
                    <span> This Ticket is {{ dialogText == true ? 'Valid' : 'Not Valid' }} </span>
                </v-card-text>
                <v-card-actions class="d-flex justify-center">
                    <v-btn color="primary" @click="dialogSuccess = false">
                        ok
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import { QrcodeCapture } from "vue-qrcode-reader";

export default {
    components: { QrcodeCapture },

    data() {
        return {
            result: "",
            qrMeet: "malaysia",
            dialogText: false,
            dialogSuccess: false,
        };
    },

    methods: {
        onDecode(n) {
            this.result = n;
            let regexResult = n.match(this.qrMeet);
            if (regexResult !== null) {
                this.dialogText = true
                this.dialogSuccess = true
            }
            else {
                this.dialogText = false
                this.dialogSuccess = true
            }
        },
    },
};
</script>
