<template>
    <div>
        <p class="error">{{ error }}</p>

        <p class="decode-result">
            Last result: <b>{{ result }}</b>
        </p>

        <qrcode-stream @decode="onDecode" @init="onInit" />

        <v-btn @click="clear"> clear </v-btn>
        <v-dialog
            v-model="dialogSuccess"
            persistent
            max-width="400"
            transition="scroll-y-transition"
        >
            <v-card class="pb-6">
                <v-card-title class="headline d-flex justify-center">
                    <v-icon
                        size="150"
                        :color="dialogText == true ? '#a5dc86' : 'red'"
                    >
                        {{
                            dialogText == true
                                ? "mdi-check-circle-outline"
                                : "mdi-close"
                        }}
                    </v-icon>
                </v-card-title>
                <v-card-text class="text-center">
                    <span class="text-h4 font-weight-bold">
                        {{ dialogText == true ? "Good Job!" : "Not Good!" }}
                    </span>
                    <br />
                    <span>
                        This Ticket is
                        {{ dialogText == true ? "Valid" : "Not Valid" }}
                    </span>
                </v-card-text>
                <v-card-actions class="d-flex justify-center">
                    <v-btn color="primary" @click="dialogSuccess = false">
                        ok
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import { QrcodeStream } from "vue-qrcode-reader";

export default {
    components: { QrcodeStream },

    data() {
        return {
            result: "",
            error: "",
            qrMeet: "malaysia",
            dialogText: false,
            dialogSuccess: false,
        };
    },

    methods: {
        onDecode(n) {
            this.result = n;
            let regexResult = n.match(this.qrMeet);
            if (regexResult !== null) {
                this.dialogText = true
                this.dialogSuccess = true
            }
            else {
                this.dialogText = false
                this.dialogSuccess = true
            }
        },

        async onInit(promise) {
            try {
                await promise;
            } catch (error) {
                if (error.name === "NotAllowedError") {
                    this.error =
                        "ERROR: you need to grant camera access permisson";
                } else if (error.name === "NotFoundError") {
                    this.error = "ERROR: no camera on this device";
                } else if (error.name === "NotSupportedError") {
                    this.error =
                        "ERROR: secure context required (HTTPS, localhost)";
                } else if (error.name === "NotReadableError") {
                    this.error = "ERROR: is the camera already in use?";
                } else if (error.name === "OverconstrainedError") {
                    this.error = "ERROR: installed cameras are not suitable";
                } else if (error.name === "StreamApiNotSupportedError") {
                    this.error =
                        "ERROR: Stream API is not supported in this browser";
                }
            }
        },

        clear() {
            this.result = "";
        },
    },
};
</script>

<style scoped>
.error {
    font-weight: bold;
    color: red;
}
</style>
